import { useState } from "react";
import colors from "../../colors";

import { Link } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";

// function
import { handleAddToWishlist } from "../functions/productFunction";

// animation
import { StyleSheet, css } from "aphrodite";
import { fadeInDown, fadeInLeft, fadeInRight } from "react-animations";

// toaser
import Toster from "toastr";
Toster.options = {
  closeButton: false,
  debug: false,
  newestOnTop: false,
  progressBar: true,
  positionClass: "toast-bottom-center",
  preventDuplicates: true,
  onclick: null,
  showDuration: "200",
  hideDuration: "1000",
  timeOut: "2000",
  extendedTimeOut: "1000",
  showEasing: "swing",
  hideEasing: "linear",
  showMethod: "fadeIn",
  hideMethod: "fadeOut",
};

const Home = () => {
  //DATA
  // const [state, setstate] = useState(initialState);
  // const products = useSelector((state) => state.Products);
  const wishlist = useSelector((state) => state.Wishlist);

  const products = useSelector((state) => state.ProductList);
  const categories = useSelector((state) => state.CategoryList);

  // dispatch init
  const dispatch = useDispatch();

  // useState
  const [data, setData] = useState(products);

  //
  const discountCal = (num, dis) => {
    return Math.floor(num - num * (dis / 100));
  };

  //
  const ratingCal = (ratings, views) => {
    return (Number(ratings) / Number(views)).toFixed(1);
  };

  // const [isInWish, setIsInWish] = useState(false);
  // const handleAddToWish = (isInWishlist, id) => {
  //   console.log("wish btn clicked! previous value: ", isInWishlist);
  //   const productId = { id };

  //   if (!isInWishlist) {
  //     dispatch(AddToWishlist(productId));
  //     Toster.success("Added To Wishlist");

  //   } else {
  //     dispatch(RemoveFromWishlist(productId));
  //     Toster.error("Removed From Wishlist");
  //   }
  // };

  // COMPONENT
  // WISH LOVE BTN
  const WishBtn = (flag, id) => {
    return (
      <div style={styles.floating} className={css(animation.anm5)}>
        <div
          className="float-right card px-1 py-0 m-2"
          style={{ borderRadius: 50 }}
        >
          <h6
            className="mb-0 pb-1 text-center m-auto"
            style={{ cursor: "pointer" }}
            onClick={() => {
              // handleAddToWish(flag, id);
              // function call
              handleAddToWishlist(flag, id, dispatch);
            }}
          >
            {flag ? (
              <img
                className={css(animation.anm5)}
                style={{ height: 16 }}
                src="/love1.png"
              />
            ) : (
              <img
                className={css(animation.anm5)}
                style={{ height: 16 }}
                src="/love0.png"
              />
            )}
          </h6>
        </div>
      </div>
    );
  };

  //
  const RatingComp = (rating, views) => {
    const ratingValue = ratingCal(rating, views);
    return (
      <h6 className="mb-1" style={{ color: colors.black }}>
        <span
          style={
            ratingValue >= 1 && ratingValue <= 2
              ? styles.ratingLow
              : ratingValue > 2 && ratingValue < 4
              ? styles.ratingMid
              : ratingValue >= 4 && ratingValue <= 5 && styles.ratingHigh
          }
          className="badge px-2 py-1"
        >
          {ratingValue}
          <i className="fa fa-star pl-1"></i>
        </span>{" "}
        <span className="text-secondary" style={{ fontSize: 13 }}>
          ({views})
        </span>
      </h6>
    );
  };

  // Cart Item
  const CartItem = (info, category, i) => {
    // console.log(" >>  ",categoryId);
    return (
      <div key={i} className="col-lg-3 col-md-4 col-sm-4 p-1 ">
        <div className={css(animation.anm1)}>
          {/* <div className="row mx-0"> */}
          {/*  */}
          <div className="card">
            <div style={{ height: "auto" }}>
              {wishlist.filter((item) => item.id == info.id).length > 0
                ? WishBtn(true, info.id)
                : WishBtn(false, info.id)}

              <Link to={`/info/${category}/${info.product}/${info.id}`}>
                <img
                  className="m-auto"
                  style={styles.image}
                  src="/loding7.gif"
                  alt="Product sample photo"
                />
              </Link>
            </div>
            <div className="card-body py-1 px-1 text-center">
              <Link to={`/info/${category}/${info.product}/${info.id}`}>
                <h6
                  className="mb-1"
                  style={{
                    color: colors.deepBlue,
                    fontWeight: 500,
                    fontSize: 15,
                  }}
                >
                  {info.product.substr(0, 30) + ".."}
                </h6>
              </Link>
              {/* Rating */}
              {/* <h6 className="mb-1" style={{ color: colors.black }}>
                <span className="badge badge-warning px-2 py-1">
                  {ratingCal(info.rating.totalRating, info.rating.totalView)}
                  <i className="fa fa-star pl-1"></i>
                </span>{" "}
                <span className="text-secondary" style={{ fontSize: 13 }}>
                  ({info.rating.totalView})
                </span>
              </h6> */}
              {RatingComp(info.rating.totalRating, info.rating.totalView)}
              {/* Pricing */}
              <p
                style={{ fontWeight: 600, fontSize: 14 }}
                className="text-dark mb-1"
              >
                &#8377;{discountCal(info.pricing.price, info.pricing.discount)}
                <span
                  style={{
                    fontSize: 13,
                    color: "#a7a7a7",
                    textDecoration: "line-through",
                  }}
                  className="pl-1"
                >
                  &#8377;{info.pricing.price}
                </span>{" "}
                <span className="text-success" style={{ fontSize: 13 }}>
                  {info.pricing.discount}% off
                </span>
              </p>
            </div>
          </div>
          {/*  */}
        </div>
        {/* </div> */}
      </div>
    );
  };

  ///////////////////
  const CategoryHeader = (info, i) => {
    return (
      <div key={i} className="col-12 px-1">
        <div className={css(animation.anm10)}>
          <div className="card">
            <div className="card-header py-2">
              <div className="container">
                <h5 className="mb-0 text-secondary m-auto ">
                  <span style={{ fontWeight: 600, fontSize: 20 }}>
                    {info.category}
                  </span>
                </h5>
                <span
                  className="text-secondary"
                  style={{ fontWeight: 400, fontSize: 15 }}
                >
                  {info.description}
                </span>
              </div>
            </div>
          </div>
        </div>
        <div className="container">
          <div className="row mx-0 ">
            {data
              .filter((item) => item.category == info.id)
              .map((product, j) => {
                return CartItem(product, info.category, j);
              })}
          </div>
        </div>
      </div>
    );
  };

  // #######
  return (
    <div className="row mx-0">
      <div className="col px-1">
        {categories.map((category, i) => {
          return (
            <div key={i} className="row mx-0 justify-content-center mb-4">
              {CategoryHeader(category, i)}
            </div>
          );
        })}
      </div>
    </div>
  );
};

const styles = {
  image: {
    height: "auto",
    width: "100%",
    position: "relatve",
    margin: "auto",
  },
  floating: {
    position: "absolute",
    top: "0px",
    right: "0px",
  },

  ratingLow: {
    backgroundColor: "#e34c10",
    color: "#fff",
  },

  ratingMid: {
    backgroundColor: "#f0bd05",
    color: "#fff",
  },

  ratingHigh: {
    backgroundColor: "#549917",
    color: "#fff",
  },
};

// animation
const animation = StyleSheet.create({
  anm1: {
    animationName: fadeInLeft,
    animationDuration: "1.5s",
  },
  anm10: {
    animationName: fadeInRight,
    animationDuration: "1.5s",
  },

  anm3: {
    animationName: fadeInDown,
    animationDuration: "3.5s",
  },
});

export default Home;
