import { ADD_TO_CART, REMOVE_FROM_CART, CART_ITEM_INCREMENT, CART_ITEM_DECREMENT } from "../main/actoinTypes";
import { MyAccount } from "../main/initData";

export const MyCartReducer = (state = MyAccount, action) => {
  switch (action.type) {
    case ADD_TO_CART:
      return state.mycart.push(action.payload);
    
    case REMOVE_FROM_CART:
        return state.mycart.filter((item) => item.id !== action.payload.id);

    case CART_ITEM_INCREMENT:
      return state.mycart.filter((item) => item.id == action.payload.id)[0]
        .count++;

    case CART_ITEM_DECREMENT:
      return state.mycart.filter((item) => item.id == action.payload.id)[0]
        .count--;

    default:
      return state;
  }
};
