import {ADD_TO_WISH,  REMOVE_FROM_WISH} from '../main/actoinTypes'


export const AddToWishlist = (data)=>{
    const payload = data
    const returnObject = {
        type : ADD_TO_WISH,
        payload : data
    }

    return returnObject;
}

export const RemoveFromWishlist = (data)=>{
    const payload = data
    const returnObject = {
        type : REMOVE_FROM_WISH,
        payload : data
    }

    return returnObject;
}

