import { createStore, combineReducers } from "redux";
// import counterReducer from "../reducers/counterReducer";
import { accountReducer } from "../reducers/accountReducer";
import { MyCartReducer } from "../reducers/mycartReducer";
import { WishListReducer } from "../reducers/wishlistReducer";
import { ProductsReducer, AllProductsReducer, AllCategoriesReducer } from "../reducers/productReducer";

// combine all reducer
const all = combineReducers({
  Account: accountReducer,
  Mycart: MyCartReducer,
  Wishlist: WishListReducer,
  Products : ProductsReducer,
  
  ProductList : AllProductsReducer,
  CategoryList : AllCategoriesReducer
});

// create the store for globaly access
const store = createStore(
  all,
  window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__()
);

export default store;
