import {AddToWishlist, RemoveFromWishlist} from '../../STORE/actions/wishlistAction' 
import Toster from "toastr";
Toster.options = {
  closeButton: false,
  debug: false,
  newestOnTop: false,
  progressBar: true,
  positionClass: "toast-bottom-center",
  preventDuplicates: true,
  onclick: null,
  showDuration: "200",
  hideDuration: "1000",
  timeOut: "2000",
  extendedTimeOut: "1000",
  showEasing: "swing",
  hideEasing: "linear",
  showMethod: "fadeIn",
  hideMethod: "fadeOut",
};


export const handleAddToWishlist = (isInWishlist, id, dispatch) => {
  const productId = { id };

  if (!isInWishlist) {
    dispatch(AddToWishlist(productId));
    Toster.success("New Product Added To Wishlist" ,"Wishlist Notificaton");
  } else {
    dispatch(RemoveFromWishlist(productId));
    Toster.error("One Product Removed From Wishlist", "Wishlist Notificaton");
  }
};
