import { useState } from "react";
import colors from "../../colors";
import { Link } from "react-router-dom";
// animation
import { fadeInDown } from "react-animations";
import { StyleSheet, css } from "aphrodite";

// store related
import { UpdateAccount } from "../../STORE/actions/accountAction";
import store from "../../STORE/main/myStore";
import { useSelector, useDispatch } from "react-redux";

// toster
import Toster from "toastr";
Toster.options = {
  closeButton: false,
  debug: false,
  newestOnTop: false,
  progressBar: true,
  positionClass: "toast-bottom-center",
  preventDuplicates: true,
  onclick: null,
  showDuration: "200",
  hideDuration: "1000",
  timeOut: "2000",
  extendedTimeOut: "1000",
  showEasing: "swing",
  hideEasing: "linear",
  showMethod: "fadeIn",
  hideMethod: "fadeOut",
};

const MyAccount = () => {
  //DATA from global store
  const Account = useSelector((state) => state.Account);
  const UpdateAccountDispatch = useDispatch();

  // log
  console.log(Account);

  // component data
  const [data, setData] = useState(Account);
  const [isDataEdited, setIsDataEdited] = useState(false);
  const [isReadonly, setIsReadonly] = useState(true);

  // component action handler
  const handleName = (e) => {
    e.preventDefault()
    setData({
      ...data,
      name: e.target.value,
    });
    setIsDataEdited(true);
  };

  const handleEmail = (e) => {
    e.preventDefault()
    setData({
      ...data,
      email: e.target.value,
    });
    setIsDataEdited(true);
  };

  const handleMobile = (e) => {
    e.preventDefault()
    setData({
      ...data,
      mobile: e.target.mobile,
    });
    setIsDataEdited(true);
  };

  // update
  const handleUpdateAccount = () => {
    console.log("Update clicked");
    if (isDataEdited) {
      UpdateAccountDispatch(UpdateAccount(data));
      setIsDataEdited(false);
      setIsReadonly(true);
      
      Toster.success("Account is updated!","Account Notification");
    }

  };

  const handleCancelUpdate = () => {
    setIsDataEdited(false);
    setData(Account);
    setIsReadonly(true);
  };

  const handleEnableEdit = () => {
    setIsReadonly(false);
    console.log(setIsReadonly);
  };

  // Cart Item
  const Section = () => {
    return (
      <div>
        {/* SECTON 1 */}
        <div className={css(animation.anm1)} style={{ marginBottom: 8 }}>
          <div className="card shadow">
            {/* CARD HEADER */}
            <div className="card-header py-3">
              <h6
                className="mb-0"
                style={{ color: colors.black, fontWeight: 600 }}
              >
                SECTION
              </h6>
            </div>
            {/* CARD BODY */}
            <div className="card-body p-0"></div>
            {/*  */}
          </div>
        </div>
      </div>
    );
  };

  const PhotoCard = () => {
    return (
      <div className={css(animation.anm3)}>
        <div className="card ">
          <div className="m-auto">
            <img
              className="m-auto"
              src="/loding7.gif"
              alt="Card image cap"
              style={styles.image}
            />
          </div>
          <div className="card-body py-3">
            <h5 className="text-center" style={{ fontWeight: 500 }}>
              {Account.name}
            </h5>

            {/* User Information */}
            <div className="row">
              {/* name */}
              <div className="col-lg-11 col-md-11 m-auto py-2">
                {/* <div className="form-group"> */}
                <input
                  type="text"
                  className="form-control"
                  placeholder="Name.."
                  readOnly={isReadonly}
                  value={data.name}
                  onChange={handleName}
                />
                {/* </div> */}
              </div>
              {/* email */}
              <div className="col-lg-11 col-md-11 m-auto py-2">
                {/* <div className="form-group"> */}
                <input
                  type="email"
                  className="form-control"
                  placeholder="Email.."
                  readOnly={isReadonly}
                  value={data.email}
                  onChange={handleEmail}
                />
                {/* </div> */}
              </div>
              {/*  */}
              {/* name */}
              <div className="col-lg-11 col-md-11 m-auto py-2">
                {/* <div className="form-group"> */}
                <input
                  type="phone"
                  className="form-control"
                  placeholder="Mobile.."
                  readOnly={isReadonly}
                  value={data.mobile}
                  onChange={handleMobile}
                />
                {/* </div> */}
              </div>
            </div>
            {/*  */}
            {isReadonly ? (
              <div className="col-lg-11 col-md-11 m-auto text-center">
                <button onClick={handleEnableEdit} className="btn btn-info">
                  edit
                </button>
              </div>
            ) : (
              <div className="col-lg-11 col-md-11 m-auto text-center justify-content-around">
                <button
                  onClick={handleUpdateAccount}
                  className="btn btn-warning active mr-1"
                >
                  update
                </button>
                <button
                  onClick={handleCancelUpdate}
                  className="btn btn-secondary active ml-1"
                >
                  Cancel
                </button>
              </div>
            )}
          </div>
        </div>
      </div>
    );
  };

  // #######
  return (
    <div className="row mx-0">
      <div className="col-lg-10 m-auto px-0">
        <div className="row mx-0 m-auto">
          {/*  */}
          {/* Impage */}
          <div className="col-lg-4 col-md-4 col-sm-4 px-2 mb-2">
            {PhotoCard()}
          </div>
          {/* Section 1 */}
          <div className="col-lg-8 col-md-4 col-sm-4 px-2 mb-2">
            {Section()}
            {Section()}
          </div>
          {/*  */}
        </div>
      </div>
    </div>
  );
};

const styles = {
  image: {
    height: "auto",
    width: "100%",
    position: "relatve",
    margin: "auto",
  },
};

// animation
const animation = StyleSheet.create({
  anm1: {
    animationName: fadeInDown,
    animationDuration: "1.5s",
  },

  anm3: {
    animationName: fadeInDown,
    animationDuration: "2.5s",
  },
});

export default MyAccount;
