import { ADD_TO_WISH, REMOVE_FROM_WISH } from "../main/actoinTypes";
import { Wishlist } from "../main/initData";

export const WishListReducer = (state = Wishlist, action) => {
  // console.log("action:: ", action);
  // console.log("wishlist:: ", state);
  switch (action.type) {
    case ADD_TO_WISH:
      if (
        state.filter((item) => item.id == action.payload.id).length < 1
      )
        state = [...state, action.payload];
      return state;

    case REMOVE_FROM_WISH:
      return state.filter((item) => item.id != action.payload.id);

    default:
      return state;
  }
};
